/* Container */
.comingsoon-container {
  background-color: #000;
  color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 32px;
  border-bottom: 1px solid white;
}

.logo {
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 1px;
}

.logo-highlight {
  color: #b3b3b3;
}
.logo-img {
  height: 50px;   
  width: 100px;
  object-fit: contain;
}

.menu-btn {
  background: none;
  border: none;
  color: #fff;
  font-size: 15px;
  letter-spacing: 0.5px;
  cursor: pointer;
}

.menu-icon {
  margin-left: 5px;
  font-size: 18px;
}

/* Main Content */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.rocket-img {
  width: 110px;
  margin-bottom: 14px;
}

.main-title {
  font-size: 32px;
  font-weight: bold;
  letter-spacing: 1px;
  margin-bottom: 1px;
}

.subtitle {
  color: white;
  font-size: 16px;
   margin-top: 0; 
}

/* Footer */
.footer {
  border-top: 1px solid white;
  padding: 20px 180px;
  font-size: 14px;
  color: white;
}

.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 8px;
  border-bottom: 1px solid white;
}

.footer-links {
  display: flex;
  gap: 40px;
}

.footer-links a {
  color: white;
  text-decoration: none;
}

.footer-links a:hover {
  color: #fff;
}

/* Social Icons */
.social-icons {
  display: flex;
  gap: 10px;
  align-items: center;
}

.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  background: none;
  color: white;
  border: none;
  padding: 0;
  cursor: pointer;
}

.icon:hover {
  color: #ccc;
}

.youtube-icon,
.linkedin-icon,
.twitter-icon
{
  background: none !important;
  border-radius: 0;
}
.twitter-icon svg {
  color: white;
  stroke: white;
  stroke-width: 30px;
}
/* Instagram outlined with white background */

/* Footer bottom */
.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
}

.small-links {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.small-links a {
  color: white;
  text-decoration: none;
}

.small-links a:hover {
  color: #fff;
}

.small-links span {
  color: white;
}

.copyright {
  font-size: 13px;
  color: white;
}/* Menu Overlay (White Bar) */
.menu-overlay {
  position: fixed;
  top: 3%;
  left:5%;
  width: 86%; 
  height: 40px;
  background: #fff;
  color: #000;
  z-index: 2000;
  display: flex;
  align-items: center;
  padding: 12px 32px;
  border-radius: 6px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}


.menu-links {
  display: flex;
  gap: 28px;
  flex: 1;
  justify-content: center;
  margin-right: 50%;
}

.menu-links a {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: #000;
}

.menu-links a:hover {
  color: #444;
}

/* Right Close Button */
.menu-close-btn {
  position: absolute;
  top: 14px;          /* distance from top */
  right: 20px;        /* distance from right */
  background: none;
  color: #000;        /* black cross */
  font-size: 24px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  z-index: 5000;
}


.menu-logo img {
 height: 50px;   
  width: 100px;
  object-fit: contain;
}

/* Mobile Responsive Fix */
@media (max-width: 768px) {
  /* Remove header line */
  .header {
    border-bottom: none;
  }

  /* Footer adjustments */
  .footer {
    border-top: none;
    padding: 20px;
    text-align: left; /* align everything left */
  }

  /* Top links (Home, Story, Career...) */
  .footer-links {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    gap: 0; /* no big gaps */
    margin-bottom: 15px;
  }

  .footer-links a {
    display: block;
    width: 100%;
    padding: 10px 0;
    border-bottom: 1px solid #444; /* thin separator line */
    text-align: left;
  }

  .footer-links a:last-child {
    border-bottom: none;
  }

  /* Middle row (Privacy Policy, Terms, etc.) */
  .small-links {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    justify-content: flex-start;
    margin-bottom: 15px;
  }

  .small-links span {
    display: none; /* remove the dots */
  }

  /* Social icons row */
  .social-icons {
    justify-content: flex-start;
    gap: 16px;
    margin-top: 10px;
    margin-bottom: 15px;
  }

  /* Copyright smaller */
  .copyright {
    font-size: 12px;
    margin-top: 5px;
    text-align: left;
  }

  /* Main text sizes */
  .main-title {
    font-size: 24px;
  }

  .subtitle {
    font-size: 14px;
  }
}


